@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@300;900&display=swap);

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}

body{
  background: url(/CovidTracker/static/media/bg.57fd86d3.jpg);
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  opacity: 1;
  font-family: 'Noto Sans TC', sans-serif;
}
.heading{
  text-align: center;
  color: #fff;
  margin-top: 3.5rem;
  font-size: 2.8rem;
  text-transform: uppercase;
}
.heading1{
  text-align: center;
  color: #fff;
  font-size: 1.4rem;
  margin-top: 1rem;
}
#container{
  background: #fff;
  width: 30rem;
  margin: 5rem auto;
  border-radius: 10px;
  padding: 3rem;
  box-shadow: 0px 2px 8px 0px rgba(255, 255, 255, 0.911);
}
#info{
  margin-bottom: 1.2rem;
}
#info p {
font-size: 1.8rem;
text-align: center;
margin-bottom: 10px;

}
#info h3{
  font-size: 2.8rem;
  text-align: center;
  font-weight: bold;
  
}
@media(max-width:500px){
  #container{
    width: 20rem;
  }
  .heading{
    font-size: 2.5rem;
  }
}

 

